           create the Dream Day, Your Way -\"Best Online Indian\n                            wedding planner tool for all your wedding needs\"\n                        </span>\n                    </div>\n                    <!-- <div>\n                        <button type=\"button\">Learn more</button>\n                        <button type=\"button\">\n                            <img\n                                src=\"/assets/landing_page/play_icon.svg\"\n                                alt=\"\"\n                            />\n                            <span> How it works </span>\n                        </button>\n                    </div> -->\n                </div>\n            </div>\n            <div class=\"row sec-one-second-box\">\n                <div>\n                    <div class=\"main_nav_links\">\n                        <a href=\"/wedding-venues\">\n                            <img\n                                src=\"/assets/landing_page/Frame_71.png\"\n                                alt=\"\"\n                            />\n                            <p>Wedding venues</p>\n                        </a>\n                        <a href=\"/ideas-and-inspiration\">\n                            <img\n                                src=\"/assets/landing_page/Frame_73.png\"\n                                alt=\"\"\n                            />\n                            <p>Ideas & Inspiration</p>\n                        </a>\n                        <a href=\"/manager-tools\">\n                            <img\n                                src=\"/assets/landing_page/Frame_74.png\"\n                                alt=\"\"\n                            />\n                            <p>Manager tools</p>\n                        </a>\n                        <!-- <a href=\"/directory\">\n                            <img\n                                src=\"/assets/landing_page/Frame_72.png\"\n                                alt=\"\"\n                            />\n                            <p>Wedding vendors</p>\n                        </a> -->\n                    </div>\n                </div>\n            </div>\n            <div class=\"sec-one-main-img\">\n                <img\n                    style=\"width: 100%\"\n                    src=\"/assets/landing_page/first.png\"\n                    alt=\"\"\n                />\n            </div>\n            <!-- <div class=\"sec-one-badge\">\n                <img class=\"mr-2\" src=\"/assets/landing_page/badge.svg\" alt=\"\" />\n                Best Quality\n            </div> -->\n        </div>\n        <div id=\"section-2\" class=\"container-fluid position-relative\">\n            <h2>Discover everything you need to plan for your Special Day</h2>\n            <h3>\n                If you're planning to get married in India, there are handful of\n                options when it comes to finding the perfect venue, vendors, and\n                inspiration for your marriage day. happywed is the best online\n                wedding Planner in India, to make your wedding planning process\n                as smooth and stress-free as possible.\n            </h3>\n            <div>\n                <a href=\"/manager-tools\">\n                    <div><img src=\"/assets/landing_page/3.png\" alt=\"\" /></div>\n                    <p>Manager tools</p>\n                    <p>Guaranteed Planning for Events</p>\n                </a>\n                <a href=\"/ideas-and-inspiration\">\n                    <div><img src=\"/assets/landing_page/11.png\" alt=\"\" /></div>\n                    <p>Ideas & Inspiration</p>\n                    <p>Incredible Ideas for Events</p>\n                </a>\n                <a href=\"/wedding-venues\">\n                    <div><img src=\"/assets/landing_page/7.png\" alt=\"\" /></div>\n                    <p>Wedding venues</p>\n                    <p>Banquet Halls, Lawns / Farmhouses, Resorts</p>\n                </a>\n                <a href=\"/directory\">\n                    <div><img src=\"/assets/landing_page/5.png\" alt=\"\" /></div>\n                    <p>Wedding vendors</p>\n                    <p style=\"color: #138ab3\">Coming Soon...</p>\n                </a>\n            </div>\n        </div>\n        <div\n            id=\"section-3\"\n            class=\"container-md\"\n            style=\"margin-top: 4em; margin-bottom: 4em\"\n        >\n            <div>\n                <div style=\"width: 50%\">\n                    <span></span>\n                    <h3>\n                        Let us help you find the perfect wedding venue for your\n                        wedding day\n                    </h3>\n                </div>\n                <!-- <p>\n                    Whether you're looking for a traditional venue or something\n                    more unique, <a :href=\"route('welcome')\">happywed</a> has\n                    everything you need to make your dream wedding a reality. We\n                    work with some of the best wedding venues in the country to\n                    provide you with a diverse range of options that cater to\n                    every budget and taste.\n                </p> -->\n                <p>\n                    Whether you're looking for a traditional venue or something\n                    more unique, Discover everything you need to make your dream\n                    wedding a reality. We work with some of the best wedding\n                    venues in India to provide you with a diverse range of\n                    options that cater to every budget and taste.\n                </p>\n            </div>\n            <div>\n                <div>\n                    <img src=\"/assets/landing_page/23.png\" alt=\"\" />\n                    <div class=\"overlay-blog\"></div>\n                    <p>\n                        <span>Resorts</span>\n                        Have your wedding at a conservatory. Consider\n                        conservatories, botanical gardens, and horticulture\n                        centers.\n                    </p>\n                </div>\n                <div>\n                    <img src=\"/assets/landing_page/19.png\" alt=\"\" />\n                    <div class=\"overlay-blog\"></div>\n                    <p>\n                        <span>Destination</span>\n                        Destinations are perfect for couples who want to add a\n                        touch of adventure and excitement to their wedding day.\n                    </p>\n                </div>\n                <div>\n                    <img src=\"/assets/landing_page/21.png\" alt=\"\" />\n                    <div class=\"overlay-blog\"></div>\n                    <p>\n                        <span>Beach</span>\n                        The Beach offers stunning views and natural beauty that\n                        will make your wedding day unforgettable.\n                    </p>\n                </div>\n                <div>\n                    <img src=\"/assets/landing_page/22.png\" alt=\"\" />\n                    <div class=\"overlay-blog\"></div>\n                    <p>\n                        <span>Hotels</span>\n                        Hotels offer the perfect blend of modern amenities and\n                        classic elegance, making them the perfect choice for a\n                        timeless and unforgettable wedding day.\n                    </p>\n                </div>\n            </div>\n        </div>\n        <div id=\"section-4\" class=\"container-fluid\">\n            <div class=\"row\" style=\"margin: 5em\">\n                <div class=\"col-lg-4 sec-4-head\">\n                    <h3>Smart tools to Manage your wedding</h3>\n                    <p>\n                        Manage your dream wedding with ease using our Wedding\n                        Manager Tools. From creating beautiful invitations to\n                