s to align\n                            with clients wishes\n                        </p>\n                    </div>\n                </div>\n            </div>\n        </div> -->\n        <div id=\"section-7\">\n            <h1>Happy stories</h1>\n            <div class=\"position-relative\" style=\"width: 90%; margin: auto\">\n                <img src=\"/assets/landing_page/Ornament.svg\" alt=\"\" />\n                <div class=\"splide\" id=\"landing-page-testimonial\">\n                    <div class=\"splide__arrows\">\n                        <button\n                            class=\"splide__arrow splide__arrow--prev\"\n                            style=\"\n                                border-radius: 5.86667px;\n                                width: 25px;\n                                height: 25px;\n                                left: -1.5em;\n                            \"\n                        >\n                            <i class=\"fas fa-angle-left\"></i>\n                        </button>\n                        <button\n                            class=\"splide__arrow splide__arrow--next\"\n                            style=\"\n                                border-radius: 5.86667px;\n                                width: 25px;\n                                height: 25px;\n                                right: -1.5em;\n                            \"\n                        >\n                            <i class=\"fas fa-angle-right\"></i>\n                        </button>\n                    </div>\n                    <div class=\"splide__track\">\n                        <ul class=\"splide__list d-flex\">\n                            <li\n                                v-for=\"(item, index) in testimonials\"\n                                :key=\"index\"\n                                class=\"splide__slide\"\n                                style=\"border: none\"\n                            >\n                        