500.svg\"\n                                                alt=\"\"\n                                            />\n                                            <img\n                                                src=\"/assets/landing_page/ic_sharp-star-half.svg\"\n                                                alt=\"\"\n                                            />\n                                        </div>\n                                        <h2>\n                                            {{ item.title }}\n                                        </h2>\n                                        <p>\n                                            {{ item.body }}\n                                        </p>\n                                    </div>\n                                </div>\n                            </li>\n                        </ul>\n                    </div>\n                </div>\n                <!-- <div style=\"display: flex; position: relative; z-index: 999\">\n                    <div class=\"landing-testi-single\">\n                        <div>\n                            <img src=\"/assets/landing_page/testi2.png\" alt=\"\" />\n                        </div>\n                        <div>\n                            <h5>May 8, 2020</h5>\n                            <h2>\n                                Lorem ipsum dolor sit amet, consectetur\n                                adipiscing elit.\n                            </h2>\n                            <p>\n                                Lorem ipsum dolor sit amet, consectetur\n                                adipiscing elit. Cursus nibh mauris, nec turpis\n                                orci lectus maecenas. Suspendisse sed magna eget\n                                nibh in turpis. Consequat duis diam lacus arcu.\n                                Faucibus venenatis felis id augue sit cursus\n                                pellentesque enim arcu. Elementum felis mag