Width: true,\n                padding: \"20%\",\n                focus: \"center\",\n                breakpoints: {\n                    968: {\n                        perPage: 2,\n                    },\n                    768: {\n                        perPage: 1,\n                        autoWidth: false,\n                        padding: \"10px\",\n                    },\n                    567: {\n                        padding: \"0px\",\n                        arrows: false,\n                        wheel: true,\n                        direction: \"ltr\",\n                        releaseWheel: false,\n                        pagination: false,\n                    },\n                },\n            });\n            this.cont.mount();\n            // _this.cont.on(\"pagination:updated\", function (data, prev, curr) {\n            //     console.log(\"changed\", data.list.children, prev, curr);\n            //     data.list.children.forEach(function (item) {\n            //         console.log(\n            //             item,\n            //             curr.button.parentElement.isSameNode(item)\n            //         );\n            //     });\n            // });\n        },\n    },\n    mounted() {\n        document.onreadystatechange = () => {\n            if (document.readyState == \"complete\") {\n                this.testimonialsSplide();\n            }\n        };\n        // /window.addEventListener(\"resize\", this.setHeight);\n    },\n    computed: {\n        getUrl() {\n            let currentUrl = window.location.href;\n            return '<a :href=\"currentUrl\">happywed</a>';\n        },\n    },\n};\n</script>\n\n<style scoped>\n/* * {\n    border: 1px solid red;\n} */\n.sec-one-box {\n    background: #461952;\n    border-radius: 13.4746px;\n    padding: 12px;\n    height: max-content;\n    margin-right: 12px;\n    margin-top: 14px;\n}\n.sec-one-box > div {\n    background: #592268;\n    border-radius: 50%;\n    max-width: 85px;\n    max